import React from 'react';
import './Contents.scss';

const MARKDOWN = require('showdown');
const MARKDOWN_CONVERTER = new MARKDOWN.Converter({
    tables:true,
    strikethrough:true
});

export default class Contents extends React.Component {
    state = {contests: "", data: ""};

    componentDidMount() {
        if (this.props.location.search) {
            let queryMap = Contents.parseQuery(this.props.location.search);
            if (queryMap.path) {
                fetch(queryMap.path)
                    .then(response => response.text())
                    .then(responseText => this.setState({contents: MARKDOWN_CONVERTER.makeHtml(responseText), date: queryMap.date}));
            }
        }
    }

    static parseQuery(queryString) {
        let query = {};
        let pairs = (queryString[0] === '?' ? queryString.substr(1) : queryString).split('&');
        for (let i = 0; i < pairs.length; i++) {
            let pair = pairs[i].split('=');
            query[decodeURIComponent(pair[0])] = decodeURIComponent(pair[1] || '');
        }
        return query;
    }

    render() {
        return (
            <div className="contents">
                <div className="col-sm-1"/>
                <div className="col-sm-10">
                    <a onClick={() => this.props.history.goBack()}>목록으로</a>
                    <div className="date">작성일 : {this.state.date}</div>
                    <div style={{textAlign: 'left'}} dangerouslySetInnerHTML={{__html: this.state.contents}}/>
                    <a onClick={() => this.props.history.goBack()}>목록으로</a>
                </div>
                <div className="col-sm-1"/>
            </div>
        );
    }
}